import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import './feedback.css';

export default class Feedback extends Component {
    state = {
        feedback:[
            {
                feedID: 'FEED3392893',
                resvID: 'SDDM2203333',
                restID: 'RE393939403',
                restName: 'Harission',
                cname: 'Abhinav Verma',
                phone: '030304943003',
                date: '22 July, 2021',
                time: '12:00 AM',
                comment: 'Hi i really enjoyed a lot. food was awesome.'
            },
            {
                feedID: 'FEED339190903',
                resvID: 'SDDM20330876',
                restID: 'RE39300003',
                restName: 'Barbequee Hotel',
                phone: '990493934883',
                cname: 'Saher Thakur',
                date: '22 Sept, 2021',
                time: '2:00 PM',
                comment: 'It was nice to have dinner. Staff was well educated.'
            }
        ]
    }
    render() {
        return (
            <>
                <div className="container-fluid bg-gray">
                        <Link to="/restaurant/dashboard" className="h4 rs-back-btn text-decoration-none my-3 d-inline-block">
                            <i className="ti-angle-left"></i>
                            Back
                        </Link>
                </div>
                <div className="bg-gray">
                <div className="container">
                    <div className="row">
                        <div className="col-md-8 mx-auto">
                            <h5 className="text-center display-4">Check Feedback</h5>
                            <p className="text-center">Enter customer phone number to get feedback quickly.</p>
                            <div className="rs-search-inputs d-flex align-items-center border-1 border border-dark overflow-hidden bg-white">
                                <input type="text" className="form-control form-control-lg border-0" placeholder="Phone Number"/>
                                <button className="btn rs-submit-btn"><i className="ti-search"></i></button>
                            </div>
                        </div>
                    </div>

                    <div className="row mt-5 mb-2">
                        {
                            this.state.feedback.map(el => 
                                <div className="col-md-4">
                                    <div className="feedback-container">
                                        <i className="ti-quote-left"></i>
                                        <div>
                                            <label>Reservation ID</label>
                                            <h5 className="mb-3">{el.resvID}</h5>
                                            
                                            <label>Customer Name</label>
                                            <h5 className="mb-3">{el.cname}</h5>
                                            <label>Contact</label>
                                            <h5 className="mb-3">{el.phone}</h5>
                                            <div>
                                                <label>Date</label>
                                                <h5 className="mb-3">{el.date}</h5>
                                            </div>
                                            <div>
                                                <label>Time</label>
                                                <h5 className="mb-3">{el.time}</h5>
                                            </div>
                                            <label>Feedback</label>
                                            <h5>{el.comment}</h5>

                                            
                                        </div>
                                        <i className="ti-quote-right"></i>
                                    </div>
                                </div>    
                            )
                        }
                    </div>
                </div>
                </div>
            </>
        )
    }
}
