import React, { Component } from 'react'
import {Link} from 'react-router-dom'

import './index.css';
export default class CheckReservation extends Component {
    state = {
        reservations: [
            {
                resvID: 'RE121245463',
                restID: 'RT34205293',
                cname: 'Arnav saksena',
                date: '12 Aug, 2021',
                time: '10:00 AM',
                no_of_guest: 4,
                cuisine: [
                    'Chinease',
                    'South Taste',
                    'North Tadka'
                ],
                preference: 'should be air conditioned enviornment',
                status: 'awaiting'
            },
            {
                resvID: 'RE1212454236',
                restID: 'TR69762293',
                cname: 'Gaurav Purohit',
                date: '21 Aug, 2021',
                time: '05:00 PM',
                no_of_guest: 8,
                cuisine: [
                    'Chinease',
                    'South Taste',
                    'North Tadka'
                ],
                preference: 'should be family enviorment',
                status: 'confirmed'
            }
        ]
    }
    render() {
        return (
            <>
                <div className="container-fluid">
                        <Link to="/restaurant/dashboard" className="h4 rs-back-btn text-decoration-none my-3 d-inline-block">
                            <i className="ti-angle-left"></i>
                            Back
                        </Link>
                </div>
                <div className="container">
                    <div className="row">
                        <div className="col-md-8 mx-auto">
                            <h5 className="text-center display-4">Check Reservation</h5>
                            <p className="text-center">Check latest or any rerservation by just changing filter.</p>
                        </div>
                    </div>

                    <div className="row my-3 justify-content-end">
                        <div className="col-md-2 px-md-0 text-end mb-4">
                            <div className="d-inline-flex align-items-center border ps-3 py-2">
                                <label className="me-1 mb-0 d-block"><i className="ti-filter"></i></label>
                                <select className="form-select form-select-sm border-0" style={{maxWidth: '120px', marginLeft:'auto'}}>
                                    <option>Today</option>
                                    <option>Tomorrow</option>
                                    <option>This Month</option>
                                    <option>Last Month</option>
                                </select>
                            </div>
                        </div>
                        <table className="table" id="rs-reservation-list">
                            <thead>
                                <tr>
                                    <th>Sr. No</th>
                                    <th>Reservation ID</th>
                                    <th>Restaurant ID</th>
                                    <th>Customer Name</th>
                                    <th>Date</th>
                                    <th>Time</th>
                                    <th  style={{width: '100px'}}>No of Guests</th>
                                    <th style={{width: '120px'}}>Cuisine</th>
                                    <th style={{width: '150px'}}>Preferences</th>
                                    <th>Status</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    this.state.reservations.map((el, index) => 
                                        <tr>
                                            <td>{index + 1}</td>
                                            <td>{el.resvID}</td>
                                            <td>{el.restID}</td>
                                            <td>{el.cname}</td>
                                            <td>{el.date}</td>
                                            <td>{el.time}</td>
                                            <td>{el.no_of_guest}</td>
                                            <td>
                                                {el.cuisine.map(ell => 
                                                    <span>{ell}</span>
                                                ) }
                                            </td>
                                            <td>{el.preference}</td>
                                                {(el.status === "awaiting") ? <td><span className="btn btn-sm  shadow btn-warning">{el.status}</span></td> : <td><span className="btn btn-sm shadow btn-primary">{el.status}</span></td>
                                            }
                                        </tr>
                                    )
                                }
                            </tbody>
                        </table>
                    </div>
                </div>
            </>
        )
    }
}
