import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import Header from './header'
import './dashboard.css';
export default class RestaurantDashboard extends Component {
    render() {
        return (
            <div>
                <Header resName="Raddision" />
                <div className="container">
                    <div className="rs-page-header">
                        <h2>Raddision</h2>
                    </div>
                </div>
                <div className="container">
                    <div className="row">
                        <div className="col-md-4">
                            <img src="/assets/images/search-reservation.jpg" className="rs-item-img" alt="" />
                            <div>
                                <h4 className="rs-item-title">Search Reservation</h4>
                                <p className="rs-item-desc">Lorem Ipsum is simply dummy text of the printing and typesetting industry.</p>
                                <Link to="/restaurant/search-reservation" className="btn btn-primary rs-item-btn">Explore</Link>
                            </div>
                        </div>
                        <div className="col-md-4">
                            <img src="/assets/images/check-reservation.jpg" className="rs-item-img" alt="" />
                            <div>
                                <h4 className="rs-item-title">Check Reservation</h4>
                                <p className="rs-item-desc">Lorem Ipsum is simply dummy text of the printing and typesetting industry.</p>
                                <Link to="/restaurant/check-reservation" className="btn btn-primary rs-item-btn">Check</Link>
                            </div>
                        </div>
                        <div className="col-md-4">
                            <img src="/assets/images/feedback.jpg" className="rs-item-img" alt="" />
                            <div>
                                <h4 className="rs-item-title">Reservation Feedback</h4>
                                <p className="rs-item-desc">Lorem Ipsum is simply dummy text of the printing and typesetting industry.</p>
                                <Link to="/restaurant/feedback" className="btn btn-primary rs-item-btn">Get feedback</Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
