import React, { Component } from 'react'
import AdminHeader from "./AdminHeader"
import AdminSidebar from "./AdminSidebar"
import {Link} from 'react-router-dom'
import './index.css'

export default class ReservationReport extends Component {
    state = {
        reservations : [
            {
                resvID: 'RS292029102',
                restaurantName: 'Brand Gramision',
                cName: 'Harry Mahonne',
                no_of_guest: '4 Guest',
                res_date: '12 Feb 2022',
                res_time: '10:00 AM',
                status: 'waiting'
            },
            {
                resvID: 'RS2920234345',
                restaurantName: 'Harission',
                cName: 'Arnav Goswami',
                no_of_guest: '7 Guest',
                res_date: '21 Sept 2022',
                res_time: '11:00 AM',
                status: 'confirmed'
            },
            {
                resvID: 'RS453633434',
                restaurantName: 'Taj Hotel',
                cName: 'Rehman Ossi',
                no_of_guest: '2 Guest',
                res_date: '12 Nov 2021',
                res_time: '11:00 AM',
                status: 'confirmed'
            },
            {
                resvID: 'RS29233423145',
                restaurantName: 'Harission',
                cName: 'Arnav Goswami',
                no_of_guest: '4 Guest',
                res_date: '22 Aug 2021',
                res_time: '03:00 PM',
                status: 'waiting'
            }
        ]
    }
    render() {
        return (
            <div>
                <AdminHeader />
                <AdminSidebar activeLink="report" />
                <div className="rs-content">
                    <h4 className="form-title">Report Management</h4>
                    <hr/>
                    <div className="container p-4 px-5">

                        <div className="row justify-content-end">
                            <div className="col-md-3">
                                <div className="w-200 border ms-auto"> 
                                    <input type="date" className="form-control border-0" />
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div  className="d-flex border">
                                    <input type="text" className="form-control border-0" placeholder="filter list by restaurant name..." />
                                    <button className="btn "><i className="ti-search small"></i></button> 
                                </div>
                            </div>
                        </div>
                        <table className="table mt-4 table-hover">
                            <thead>
                                <tr>
                                    <th>Reservation ID</th>
                                    <th>Restaurant Name</th>
                                    <th>Customer Name</th>
                                    <th>No of Guest</th>
                                    <th>Date</th>
                                    <th>Time</th>
                                    <th>Status</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    this.state.reservations.map((el,index) => 
                                        <tr key={index}>
                                            <td>{el.resvID}</td>
                                            <td>{el.restaurantName}</td>
                                            <td>{el.cName}</td>
                                            <td>{el.no_of_guest}</td>
                                            <td>{el.res_date}</td>
                                            <td>{el.res_time}</td>
                                            {(el.status === "waiting") ? <td><button className="btn btn-sm badge btn-warning shadow">{el.status}</button></td> : <td><button className="btn btn-sm btn-info badge">{el.status}</button></td>}
                                        </tr>    
                                    )
                                }
                            </tbody>
                        </table>
                        <div  className="d-flex justify-content-between">
                            <p>10 Record for 1 of 4</p>
                            <ul className="pagination">
                                <li className="page-item"><Link className="page-link" to="#">Previous</Link></li>
                                <li className="page-item"><Link className="page-link" to="#">1</Link></li>
                                <li className="page-item"><Link className="page-link" to="#">2</Link></li>
                                <li className="page-item"><Link className="page-link" to="#">3</Link></li>
                                <li className="page-item"><Link className="page-link" to="#">Next</Link></li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
