import React, { Component } from 'react'
import AdminHeader from "./AdminHeader"
import AdminSidebar from "./AdminSidebar"
import './index.css'

export default class AddReservation extends Component {
    render() {
        return (
            <div>
                <AdminHeader />
                <AdminSidebar activeLink="reservation" />
                <div className="rs-content">
                    <h4 className="form-title">Add Reservation</h4>
                    <hr/>
                    <form className="container reservation-form">
                        <div className="row">
                            <div className="col-md-4 mb-4">  
                                <label>Select Restaurant</label>
                                <select className="form-select">
                                    <option>choose any one</option>
                                </select>
                            </div>
                            <div className="col-md-4 mb-4">  
                                <label>Reservation ID</label>
                                <input type="text" className="form-control" value="RS2452525252" disabled />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-4 mb-4">  
                                <label>Customer Name</label>
                                <input type="text" className="form-control" placeholder="e.g. John Doe" />
                            </div>
                            <div className="col-md-4 mb-4">  
                                <label>No of Guest</label>
                                <input type="text" className="form-control" placeholder="e.g. 4 " />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-4 mb-4">  
                                <label>Date</label>
                                <input className="form-control" type="date" />
                            </div>
                            <div className="col-md-4 mb-4">  
                                <label>Time</label>
                                <input className="form-control" type="time" />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-4 mb-4">  
                                <label>Select Cuisine</label>
                                <select className="form-select">
                                    <option>choose any one </option>
                                </select>
                            </div>
                            <div className="col-md-4 mb-4">  
                                <label>Phone Number</label>
                                <input type="text" className="form-control"  placeholder="e.g. +919394-343-343" />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-8 mb-3">  
                                <label>Any other preference</label>
                                <textarea className="form-control"  rows={4} />
                            </div>
                        </div>

                        <button className="btn btn-primary rounded-30 shadow my-4 px-4 py-3"><i className="ti-plus small d-inline-block me-2"></i>Add Reservation</button>
                    </form>
                </div>
            </div>
        )
    }
}
