import React, { Component } from 'react'
import {Link} from 'react-router-dom'

export default class AdminHeader extends Component {
    render() {
        return (
            <header className="fixed-top bg-white shadow">
                <div className="container-fluid">
                    <div className="row justify-content-between align-items-center py-2 border-bottom">
                        <div className="col-md-4">
                            <Link to="admin/dashboard" className="text-decoration-none text-secondary"><h5 className="mb-0">Remmsh<b className="text-primary">CRM</b></h5></Link>
                        </div>
                        <div className="col-md-4">
                            <div className="btn-group float-end">
                                <button type="button" className="btn dropdown-toggle fw-bold" data-bs-toggle="dropdown" data-bs-display="static" aria-expanded="false">
                                    <i className="ti-user me-2"></i>Admin
                                </button>
                                <ul className="dropdown-menu dropdown-menu-end">
                                    <li><button className="dropdown-item" type="button"><i className="ti-settings me-3"></i>Profile</button></li>
                                    <li><button className="dropdown-item" type="button"><i className="ti-power-off me-3"></i>Logout</button></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </header>
        )
    }
}
