import React, { Component } from 'react'
import {Link} from 'react-router-dom'

export default class RestaurantHeader extends Component {
    state = {
        RestaurantName: this.props.resName
    }
    render() {
        return (
            <header>
                <div className="container">
                    <div className="row justify-content-between my-4">
                        <div className="col-md-4">
                            <Link to="/restaurant/dashboard" className="text-decoration-none text-secondary"><h4 className="mb-0">Remmsh<b className="text-primary">CRM</b></h4></Link>
                        </div>
                        <div className="col-md-4">
                            <div class="btn-group float-end">
                                <button type="button" class="btn dropdown-toggle fw-bold" data-bs-toggle="dropdown" data-bs-display="static" aria-expanded="false">
                                    <i className="ti-home me-2"></i>{this.state.RestaurantName}
                                </button>
                                <ul class="dropdown-menu dropdown-menu-end">
                                    <li><button class="dropdown-item" type="button"><i className="ti-settings me-3"></i>Profile</button></li>
                                    <li><button class="dropdown-item" type="button"><i className="ti-power-off me-3"></i>Logout</button></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </header>
        )
    }
}
