import React, { Component } from 'react'
import AdminHeader from "./AdminHeader"
import AdminSidebar from "./AdminSidebar"
import './index.css'

export default class AddRestaurant extends Component {
    render() {
        return (
            <div>
                <AdminHeader />
                <AdminSidebar activeLink="restaurant" />
                <div className="rs-content">
                    <h4 className="form-title">Add Restaurant</h4>
                    <hr/>
                    <form className="container reservation-form">
                        <div className="row">
                            <div className="col-md-8 mb-4">  
                                <label>Restaurant Name</label>
                                <input type="text" className="form-control" />
                            </div>
                            
                            <div className="col-md-4 mb-4">  
                                <label>No. of table available</label>
                                <input type="text" className="form-control" placeholder="e.g. 5 tables" />
                            </div>
                            <div className="col-md-12 mb-4">  
                                <label>Address</label>
                                <div className="row">
                                    <div className="col-md-4 mb-4">
                                        <input type="text" className="form-control" placeholder="Address Line 1" />
                                    </div>
                                    <div className="col-md-4 mb-4">
                                        <input type="text" className="form-control" placeholder="Address Line 2" />
                                    </div>
                                    <div className="col-md-4 mb-4">
                                        <input type="text" className="form-control" placeholder="Pincode" />
                                    </div>
                                    <div className="col-md-4 mb-4">
                                        <input type="text" className="form-control" placeholder="City" />
                                    </div>
                                    <div className="col-md-4 mb-4">
                                        <input type="text" className="form-control" placeholder="State" />
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-4 mb-4">  
                                <label>Phone</label>
                                <input className="form-control" type="text" />
                            </div>
                            <div className="col-md-4 mb-4">  
                                <label>Email Address</label>
                                <input className="form-control" type="email" />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-8 mb-3">  
                                <label>Add Cuisine  (seperate each cuisine with ','</label>
                                <textarea className="form-control"  rows={4} />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-4 mb-4">  
                                <label>Username</label>
                                <input className="form-control" type="text" />
                            </div>
                            <div className="col-md-4 mb-4">  
                                <label>Password</label>
                                <input className="form-control" type="email" />
                            </div>
                        </div>

                        <button className="btn btn-primary rounded-30 shadow my-4 px-4 py-3"><i className="ti-plus small d-inline-block me-2"></i>Add Restaurant</button>
                    </form>
                </div>
            </div>
        )
    }
}
